body {
  width: 100vw;   /* corrected */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #f9f9f9; /* optional for better contrast */
}

#todo {
  border: 2px solid black;
  width: 400px;
  min-height: 300px; /* ensures container has some height */
  background-image: url(https://blog.lulu.com/content/images/2024/12/072120-ToDoList-Blog.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 12px 12px 15px darkkhaki;
  padding: 20px; /* spacing inside */
  border-radius: 8px; /* smoother corners */
}

input {
  margin-top: 20px; /* reduced margin */
  margin-right: 10px;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #333;
  box-shadow: 4px 4px 8px rgba(0,0,0,0.2);
}

ol {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  list-style-position: inside; /* so numbers align nicely */
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  background: rgba(255,255,255,0.8);
  padding: 8px;
  border-radius: 5px;
}
